.formulario {
    margin: 3rem 1.5rem 3.5rem 1.5rem;

    label {
        display: block;
        // font-weight: 600;
        color: lighten(black, 40);
        font-size: 1.4rem;
        margin-bottom: .8rem;
    }
    
    input:not([type = 'submit']) {
        font-size: 1.4rem;
        font-weight: 600;
        color: lighten(black, 40);
        border: .1rem solid $grisClaro;
        border-radius: .8rem;
        width: 100%;
        padding: .6rem;
    }
    
    input[type = 'submit'] {
        @include btn($azul);
        display: block;
        border-radius: 1.4rem;
        width: 100%;
        padding: .9rem;
        margin-top: 4.5rem;
    }

    .nombre-lista {
        font-size: 1.4rem !important;
        font-weight: 200 !important;
        color: lighten(black, 20) !important;
        margin-bottom: 2.5rem;
    }
}

.campo-error {
    background-color: #F9EBEA;
    border: .1rem solid darken(#FADBD8, 22.5) !important;
}

.campo-vacio, .lista-vacia {
    display: none;
    text-align: center;
    font-size: 1.4rem;
    color: #ed7d72;
    margin-bottom: 2.5rem;
}