.contenedor-listas {
    margin: 0 1.5rem;
}

.lista {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    border: .1rem solid $grisLigero;
    border-left: .4rem solid $azul;
    border-radius: .5rem 1.4rem 1.4rem .5rem;
    box-shadow: 10px 10px 9px 0 rgba(0,0,0,.06);
    padding: 1.8rem;
    margin-top: 1rem;

    &:first-child { margin-top: 3rem; }
    &:last-child { margin-bottom: 3rem; }

    p {
        &:first-child { 
            font-weight: 600;
            font-size: 1.6rem;
            color: lighten(black, 20);
            margin-bottom: 1rem;
        }
    }

    .material-icons-outlined {
        font-size: 2.25rem;
        color: lighten(black, 70);
    }

}

.opciones {
    display: flex;
}