header, main, footer { display: block }
.error-media { display: none; }

@media (min-width: 480px) {

    header, main, footer, .menu-inferior { display: none !important }

    .error-media { 
        display: block;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(135deg, #EBEDEF 0%, #85929E 100%);
        height: 100vh;
    }

    .error-mensaje {
        text-align: center;
        border: .1rem solid lighten(#5D6D7E, 10);
        border-radius: 1.5rem;
        // box-shadow: 8px 8px 9px 0px rgba(0,0,0,0.045);
        padding: 3rem 3rem;

        p { 
            color: lighten(#5D6D7E, 0);
            font-weight: 600;
            font-size: 1.6rem;
        }
    }
}