.producto {
    display: flex;
    background: white;
    align-items: center;
    border: .1rem solid $grisLigero;
    // box-shadow: 10px 10px 9px 0 rgba(0,0,0,.06);
    border-radius: 1.4rem;
    padding: 1.8rem;
    margin-bottom: 1rem;

    p { 
        flex: 3;
        font-weight: 600;
        color: lighten(black, 20);
        font-size: 1.6rem; 
    }
    label {
        color: lighten(black, 60);
        margin: 0 .8rem 0 0rem; 
    }
    input { 
        flex: 1;
    }
}

.seleccion { 
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, lighten($verde, 22.5) 100%);
    border: .1rem solid darken($verde, 7.5);
    box-shadow: 10px 10px 9px 0 rgba(0,0,0,.045);

    label { color: darken($verde, 21) }
    input { border: .1rem solid darken($verde, 7.5) !important }
}