@mixin btn($color) {
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, lighten($color, 30) 100%);
    font-family: $fuente_principal;
    text-decoration: none;
    text-align: center;
    font-size: 1.6rem;
    color: $color;
    border: .1rem solid $color;
    border-radius: 1.2rem;
    width: 18rem;
    padding: 1.8rem;
}

@mixin btn-solid($color) {
    @include btn($color);
    border: none;
    color: white;
    background: $color;
}