.menu-inferior {
    position: fixed;
    width: 100%;
    bottom: 0;
    display: flex;
    justify-content: center;
    gap: 6rem;
    background: white;
    text-align: center;
    color: lighten(black, 60);
    border-top: .1rem solid #b2babb;
    padding: .5rem 0 .3rem 0;
}

.apoyo { position: inherit } 

.material-icons-outlined {
    color: lighten(black, 40);
    font-size: 3.5rem;
}

.borrar { 
    display: none;
    color: #ed7d72 !important;
    margin-right: 1.5rem;
}

.block { display: block }

