.index, .crear, .listas, .seccion-lista {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}

.crear, .listas, .seccion-lista { background: $grisLigero }

.header {
    background: white;
    text-align: center;
    box-shadow: 0px 10px 9px 0 rgba(0,0,0,.06);
    padding: 1.8rem;

    h1 {
        margin-bottom: 1.8rem;
        span {
            color: $azul;
            border: .1rem solid $azul;
            border-radius: 1.25rem;
            padding: .5rem 1.4rem;
            margin-left: .7rem;
        }
    }
}
