.table {
    background: white;
    font-size: 1.6rem; 
    box-shadow: 10px 10px 9px 0 rgba(0,0,0,.06);
    width: 95%;
    margin: 3rem auto;   

    thead {
        th { border-bottom: none !important }
    }
    
    tr {
        display: flex;
        
        th { 
            display: flex;
            align-items: center;
            background: $azul;
            font-weight: 600;
            text-align: left;
            color: white !important;
            padding: 1rem !important;

            .material-icons-outlined { 
                font-size: 1.8rem;
                color: white;
                margin-left: .5rem;
            }

        }
        
        th, td {
            flex: 1;
            text-align: center;
            color: lighten(black, 60);
            border-bottom: .2rem solid lighten($grisClaro, 12.5);
            padding: 2rem 1rem;
            
            &:first-child { 
                text-align: left;
                color: lighten(black, 20);
                flex: 3;
            }

        }
    }
}