 html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    color: lighten(black, 30);
    font-family: $fuente_principal;
    font-size: 1.4rem;
}
h1 {
    font-size: 3.8rem;
}
p, legend, label { 
    line-height: 1.2;
    color: lighten(black, 60);
}
a {
    text-decoration: none;
    color: lighten(black, 60);
}
.bold {
    font-weight: 600;
}